<template>
<div style="width: 987px" v-on:import="reload">
    <v-dialog v-model="dialog" persistent max-width="350px">
        <v-card>
            <v-card-title class="headline">Update</v-card-title>
            <v-card-text>
                <v-text-field label="Code" v-model="d_code" />
                <v-text-field label="Description" v-model="d_description" />
                <v-text-field label="Location" v-model="d_location" />
                <v-text-field label="Order" v-model="d_order" />
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="indigo" text @click="updateRecord">Update</v-btn>
                <v-btn color="indigo" text @click="dialog = false">Cancel</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <v-dialog v-model="hiddendialog" persistent max-width="750px">
        <v-card>
            <v-card-title class="headline">Hidden</v-card-title>
            <v-card-text class="viewtable" style="padding: 0px">
                <SortedTable :values="abscodes" style="text-align: center; margin: auto;">
                    <thead>
                        <tr>
                            <th scope="col" class="code">
                                <SortLink name="code">Code</SortLink>
                            </th>
                            <th scope="col" class="description">
                                <SortLink name="description">Description</SortLink>
                            </th>
                            <th scope="col" class="location">
                                <SortLink name="location">Location</SortLink>
                            </th>
                            <th scope="col" class="order">
                                <SortLink name="order">Order</SortLink>
                            </th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody slot="body" slot-scope="sort">
                        <div v-for="abscode in sort.values" :key="abscode.absence_code_pk">
                            <tr v-if="abscode.hidden">
                                <td class="code">{{ abscode.code }}</td>
                                <td class="description">{{ abscode.description }}</td>
                                <td class="location">{{ abscode.location }}</td>
                                <td class="order">{{ abscode.order }}</td>
                                <td>
                                    <v-btn x-small icon @click="unhideRecord(abscode.absence_code_pk)">
                                        <v-icon>mdi-eye</v-icon>
                                    </v-btn>
                                </td>
                            </tr>
                        </div>
                    </tbody>
                </SortedTable>
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="indigo" text @click="hiddendialog = false">Close</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <div class="viewall_title">
        <h2>View All Absence Codes</h2>
    </div>
    <div class="viewtable">
        <SortedTable :values="abscodes" style="text-align: center; margin: auto;">
            <thead>
                <tr>
                    <th scope="col" class="code">
                        <SortLink name="code">Code</SortLink>
                    </th>
                    <th scope="col" class="description">
                        <SortLink name="description">Description</SortLink>
                    </th>
                    <th scope="col" class="location">
                        <SortLink name="location">Location</SortLink>
                    </th>
                    <th scope="col" class="order">
                        <SortLink name="order">Order</SortLink>
                    </th>
                    <th></th>
                </tr>
            </thead>
            <draggable tag="tbody" slot="body" slot-scope="sort">
                <div v-for="abscode in sort.values" :key="abscode.absence_code_pk">
                    <tr v-if="!abscode.hidden">
                        <td class="code">{{ abscode.code }}</td>
                        <td class="description">{{ abscode.description }}</td>
                        <td class="location">{{ abscode.location }}</td>
                        <td class="order">{{ abscode.order }}</td>
                        <td>
                            <v-btn x-small icon @click="editdialog(abscode.absence_code_pk)">
                                <v-icon>mdi-pencil</v-icon>
                            </v-btn>
                            <v-btn x-small icon @click="deleteRecord(abscode.absence_code_pk)">
                                <v-icon>mdi-delete</v-icon>
                            </v-btn>
                            <v-btn x-small icon @click="hideRecord(abscode.absence_code_pk)">
                                <v-icon>mdi-eye-off</v-icon>
                            </v-btn>
                        </td>
                    </tr>
                </div>
            </draggable>
        </SortedTable>
    </div>
    <v-row justify="center" style="margin-top: 20px">
        <v-btn @click="hiddendialog = true">View Deactivated Absence Code</v-btn>
    </v-row>
</div>
</template>

<script>
import draggable from "vuedraggable"

export default {
    data() {
        return {
            abscodes: [],
            dialog: false,
            d_code: '',
            d_description: '',
            d_location: '',
            d_order: '',
            updatekey: 0,
            hiddendialog: false
        }
    },
    components: {
        draggable,
    },
    methods: {
        editdialog(key) {
            this.dialog = true
            this.updatekey = key
            this.d_code = this.abscodes[key].code
            this.d_description = this.abscodes[key].description
            this.d_location = this.abscodes[key].location
            this.d_order = this.abscodes[key].order
        },
        deleteRecord(key) {
            this.$store.state.exceldata.splice(key, 1)
            this.reload()
        },
        hideRecord(key) {
            this.$store.state.exceldata[key][4] = true
            this.reload()
        },
        unhideRecord(key) {
            this.$store.state.exceldata[key][4] = false
            this.reload()
        },
        updateRecord() {
            this.dialog = false
            this.$store.state.exceldata[this.updatekey][0] = this.d_code
            this.$store.state.exceldata[this.updatekey][1] = this.d_description
            this.$store.state.exceldata[this.updatekey][2] = this.d_location
            this.$store.state.exceldata[this.updatekey][3] = this.d_order
            this.reload()
        },
        reload() {
            this.abscodes = [];
            for (let i = 0; i < this.$store.state.exceldata.length; i++) {
                this.abscodes.push({
                    absence_code_pk: i,
                    code: this.$store.state.exceldata[i][0],
                    description: this.$store.state.exceldata[i][1],
                    location: this.$store.state.exceldata[i][2],
                    order: this.$store.state.exceldata[i][3],
                    hidden: this.$store.state.exceldata[i][4],
                })
            }
        }
    },
    created: function () {
        this.reload()
    }
}
</script>



