import Vue from 'vue'
import Router from 'vue-router'
import Viewallcode from '@/components/Viewallcode'
import Addnewcode from '@/components/Addnewcode'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
    },
    {
      path: '/addnewcode',
      name: 'Add New Code',
      component: Addnewcode
    },
    {
      path: '/viewallcode',
      name: 'View All Code',
      component: Viewallcode
    }
  ]
})



