<template>
<v-app id="app">
    <div class="app-header">
        <div class="title">
            Manage Absence Codes
        </div>
        <div class="import-box">
            <div class="border-line">
            </div>
            <div class="export-file" style="margin-right:20px;">
                <v-btn @click="saveExcel" class="orange">
                    Download File
                </v-btn>
            </div>
            <div class="import-file">
                <v-btn @click="dialog = true" class="orange">
                    Import List <br>from File
                    <!-- <p>(.csv, xls, xlxs)</p> -->
                </v-btn>
            </div>
        </div>
    </div>
    <div class="main-panel">
        <v-navigation-drawer>
            <v-list>
                <v-list-item v-for="item in items" :key="item.title" link :to="item.to">
                    <v-list-item-icon>
                        <img :src="item.icon" alt="">
                    </v-list-item-icon>
                    <v-list-item-content>
                        <v-list-item-title>{{ item.title }}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>
        <router-view />
        <!-- <Menu @change-menu="onChangeMenu" />
        <dashboard v-bind:element="element" /> -->
    </div>
    <div class="app-footer">
    </div>
    <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
        <VueFullScreenFileDrop @drop='onDrop' />
        <v-card>
            <v-container fluid>
                <v-row justify="center" style="margin-top: 400px">
                    <input type="file" class="inputfile" id="myinput" @change="onFileChange">
                    <v-btn fab @click="onButton" style="margin-right: 10px">
                        <v-icon>mdi-file-excel</v-icon>
                    </v-btn>
                    <v-btn fab @click="dialog = false">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                </v-row>
            </v-container>
        </v-card>
    </v-dialog>
</v-app>
</template>

<script>
import readXlsxFile from 'read-excel-file'
import VueFullScreenFileDrop from 'vue-full-screen-file-drop'
import Viewallcode from '@/components/Viewallcode'

export default {
    components: {
        VueFullScreenFileDrop
    },
    data() {
        return {
            items: [{
                    title: 'Add New Code',
                    icon: require('@/assets/folder.png'),
                    to: '/addnewcode'
                },
                {
                    title: 'Edit Exiting Codes',
                    icon: require('@/assets/settings2.png'),
                    to: '/editcode'
                },
                {
                    title: 'View All Codes',
                    icon: require('@/assets/view_folder.png'),
                    to: '/viewallcode'
                },
                {
                    title: 'Print',
                    icon: require('@/assets/animal-paw-print.png'),
                    to: '/print'
                }
            ],
            dialog: false,
        }
    },
    methods: {
        onDrop: function (formData, files) {
            readXlsxFile(files[0]).then(rows => {
                this.$store.state.exceldata = []
                for (let i = 1; i < rows.length; i++)
                    this.$store.state.exceldata.push([rows[i][0], rows[i][1], rows[i][2], rows[i][3], false])
                this.dialog = false
                this.$router.push({path:'/'})
            })
        },
        onButton() {
            document.getElementById("myinput").click()
        },
        onFileChange(e) {
            var files = e.target.files || e.dataTransfer.files
            if (!files.length)
                return
            readXlsxFile(files[0]).then(rows => {
                this.$store.state.exceldata = []
                for (let i = 1; i < rows.length; i++)
                    this.$store.state.exceldata.push([rows[i][0], rows[i][1], rows[i][2], rows[i][3], false])
                this.dialog = false
                this.$router.push({path:'/'})
            })
        },
        saveExcel() {
            var exceldata = []
            for (let i = 1; i < this.$store.state.exceldata.length; i++) {
                exceldata.push({
                    "Code": this.$store.state.exceldata[i][0],
                    "Description": this.$store.state.exceldata[i][1],
                    "Location": this.$store.state.exceldata[i][2],
                    "Order": this.$store.state.exceldata[i][3]
                })
            }
            var json_exceldata = XLSX.utils.json_to_sheet(exceldata)
            var wb = XLSX.utils.book_new()
            XLSX.utils.book_append_sheet(wb, json_exceldata, 'Sheet1') // sheetAName is name of Worksheet
            XLSX.writeFile(wb, 'save.xlsx')
        }
    }
}
</script>

<style lang="scss">
@import './styles/minidash.scss';
</style>



