<template>
<div style="width: 987px; padding-left: 20px;padding-right: 20px;">
    <v-snackbar top color="error" v-model="error_snackbar" :timeout="2000">
        {{ message }}
        <v-btn color="white" text @click="error_snackbar = false">
            <v-icon>mdi-close</v-icon>
        </v-btn>
    </v-snackbar>
    <h1>Enter New Code Details</h1>
    <v-form ref="form" lazy-validation>
        <v-row justify="center" align="center" style="margin-bottom: 10px;border-bottom: 1px solid black">
            <v-text-field outlined v-model="new_code" label="CODE" style="width: 100px; margin-right: 10px;" required :rules="[v => !!v || 'Code is required']" />
            <v-text-field outlined v-model="new_description" label="DESCRIPTION" style="width: 150px; margin-right: 10px" required :rules="[v => !!v || 'Description is required']" />
            <v-text-field outlined v-model="new_location" label="LOCATION" style="width: 150px;margin-right: 10px" required :rules="[v => !!v || 'Location is required']" />
            <v-text-field outlined v-model="new_order" label="ORDER" style="width: 150px; margin-right: 10px" required :rules="[v => !!v || 'Order is required']" />
            <v-btn text class="btn_newadd" @click="Addnewcode"><img src="@/assets/plus-black-symbol.png"></v-btn>
            <v-btn text class="btn_newadd" @click="Cleanlist"><img src="@/assets/rubber.png"></v-btn>
        </v-row>
    </v-form>

    <v-row justify="center" align="center">
        <h3 style="font-style: italic">Recently added codes</h3>
    </v-row>

    <v-row justify="center">
        <v-col cols="12" lg="2" md="2" sm="3">
            <v-text-field label="SEARCH" v-model="search" :change="getSearch()" />
        </v-col>
        <v-col cols="12" lg="8" md="8" sm="8">
            <v-simple-table fixed-header height="350px" class="addtable">
                <thead>
                    <tr>
                        <th v-for="column in columns">
                            {{ column }}
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="user in getSearch()">
                        <td>{{ user.Code }}</td>
                        <td>{{ user.Description }}</td>
                        <td>{{ user.Location }}</td>
                        <td>{{ user.Order }}</td>
                    </tr>
                </tbody>
            </v-simple-table>
        </v-col>
    </v-row>
</div>
</template>

<script>
export default {
    data() {
        return {
            new_code: '',
            new_description: '',
            new_location: '',
            new_order: '',
            columns: ['Code', 'Description', 'Location', 'Order'],
            sortKey: 'Code',
            reverse: false,
            search: '',
            message: '',
            error_snackbar: false,
        }
    },
    methods: {
        Addnewcode() {
            if (this.$refs.form.validate()) {
                this.$store.state.newlist.push({
                    Code: this.new_code,
                    Description: this.new_description,
                    Location: this.new_location,
                    Order: this.new_order
                })
                this.$store.state.exceldata.push([this.new_code, this.new_description, this.new_location, this.new_order, false])
                this.getSearch()
                this.new_code = this.new_description = this.new_location = this.new_order = ''
                this.$refs.form.resetValidation()
            } else {
                this.message = "Input data correctly!"
                this.error_snackbar = true
            }
        },
        getSearch() {
            return this.$store.state.newlist.filter(result => result.Code.indexOf(this.search) > -1 || result.Description.indexOf(this.search) > -1 || result.Location.indexOf(this.search) > -1 || result.Order.indexOf(this.search) > -1)
        },
        Cleanlist() {
            this.$store.state.newlist = []
        }
    }
}
</script>



